import Header from './elements/Header/element';
import Map from './elements/Map/element';
import List from './elements/List/element';
import Footer from './elements/Footer/element';

function App() {
    return (
        <>
            <Header />
            <main id='main'>
                <Map />
                <List />
                <Footer />
            </main>
        </>
    );
}

export default App;
