import './css.css';
import { useEffect, useState } from 'react';
import { useMap } from '../Map/store';
import { useNotice } from '../Header/notice';

function Element() {
    const { setNotice } = useNotice();
    const { features, removeFeature, removeAll } = useMap();
    const [coefficient, setCoefficient] = useState(1);
    const [gosiYear, setGosiYear] = useState(new Date().getFullYear());
    const [totalArea, setTotalArea] = useState(0);
    const [totalJiga, setTotalJiga] = useState(0);

    useEffect(() => {
        let area = 0;
        for (let feature of features) {
            area = area + Number(feature.get('area') || 0);
        }
        setTotalArea(area);

        let price = 0;
        for (let feature of features) {
            price = price + (feature.get('area') || 0) * getJiga(feature, gosiYear);
        }
        setTotalJiga(price);
    }, [features, gosiYear]);

    const copy = (text) => {
        if (!text || text === '-') return;
        navigator.clipboard.writeText(text)
            .then(() => {
                setNotice(' !', 1000);
            })
            .catch(() => {
                setNotice(' !', 1000);
            });
    }

    return (
        <section id='list'>
            <div className='noise' />
            <h1 style={{ textAlign: 'center', }}></h1>
            <table className='list'>
                <thead>
                    <tr style={{ fontWeight: '700', fontSize: 'small', lineHeight: '2rem', }}>
                        <td style={{ border: 'none', width: '4rem', }}></td>
                        <td style={{ border: 'none', width: 'auto', }}></td>
                        <td style={{ border: 'none', width: '6rem', }}></td>
                        <td style={{ border: 'none', width: '10rem', }}></td>
                        <td style={{ border: 'none', width: '8rem', cursor: 'pointer', }} className='btn' onClick={() => { setCoefficient(coefficient === 1 ? 0.3025 : 1); }}>{coefficient === 1 ? '' : ''} <i className='fa-solid fa-arrow-right' /> {coefficient === 1 ? '' : ''}</td>
                        <td style={{ border: 'none', width: '8rem', cursor: 'pointer', }} className='btn'> <input className='year' type='number' dir='rtl' min={1989} max={new Date().getFullYear()} defaultValue={gosiYear} onChange={(e) => { setGosiYear(Number(e.target.value)); }} /></td>
                        <td style={{ border: 'none', width: '5rem', cursor: 'pointer', }} className='btn' onClick={() => { download(features); }}><i className='fa-solid fa-download' /> </td>
                        <td style={{ border: 'none', width: '5rem', cursor: 'pointer', }} className='btn' onClick={() => { removeAll(); }}><i className='fa-solid fa-trash-can' /> </td>
                        <td style={{ border: 'none', width: '2rem', }}></td>
                    </tr>
                    <tr style={{ fontWeight: '700', }}>
                        <td style={{ borderBottomWidth: '0', }}></td>
                        <td style={{ borderBottomWidth: '0', }}></td>
                        <td style={{ borderBottomWidth: '0', }}></td>
                        <td style={{ borderBottomWidth: '0', }}></td>
                        <td style={{ borderBottomWidth: '0', }}></td>
                        <td style={{ borderBottomWidth: '0', }}></td>
                        <td style={{ borderBottomWidth: '0', }} colSpan={2}></td>
                        <td style={{ border: 'none', }}></td>
                    </tr>
                    <tr style={{ fontSize: 'small', }}>
                        <td style={{ borderTopWidth: '0', }}></td>
                        <td style={{ borderTopWidth: '0', }}></td>
                        <td style={{ borderTopWidth: '0', }}></td>
                        <td style={{ borderTopWidth: '0', }}></td>
                        <td style={{ borderTopWidth: '0', }}>{coefficient === 1 ? '()' : '()'}</td>
                        <td style={{ borderTopWidth: '0', }}>{coefficient === 1 ? '(/)' : '(/)'}</td>
                        <td style={{ borderTopWidth: '0', }} colSpan={2}></td>
                        <td style={{ border: 'none', }}></td>
                    </tr>
                </thead>
                <tbody>
                    <tr style={{ fontWeight: '700' }}>
                        <td style={{ backgroundColor: 'var(--color-gray)', borderRightWidth: '0', }}>{features.length || '-'}</td>
                        <td style={{ backgroundColor: 'var(--color-gray)', borderWidth: '1px 0', textAlign: 'left', }}>{features.length > 0 && ''}</td>
                        <td style={{ backgroundColor: 'var(--color-gray)', borderWidth: '1px 0', }}></td>
                        <td style={{ backgroundColor: 'var(--color-gray)', borderWidth: '1px 0', }}></td>
                        <td style={{ backgroundColor: 'var(--color-gray)', borderWidth: '1px 0', textAlign: 'right', cursor: 'pointer', }} title=' ' onClick={(e) => { copy(e.target.textContent); }}>{totalArea > 0 ? (totalArea * coefficient).toLocaleString('ko-KR', { minimumFractionDigits: 1, maximumFractionDigits: 1, }) : '-'}</td>
                        <td style={{ backgroundColor: 'var(--color-gray)', borderWidth: '1px 0', textAlign: 'right', cursor: 'pointer', }} title=' ' onClick={(e) => { copy(e.target.textContent); }}>{totalJiga > 0 ? (totalJiga / 1000).toLocaleString('ko-KR', { maximumFractionDigits: 0, }) : '-'}</td>
                        <td style={{ backgroundColor: 'var(--color-gray)', borderLeftWidth: '0', textAlign: 'left', }} colSpan={2}>{totalJiga > 0 && ''}</td>
                        <td style={{ border: 'none', }}></td>
                    </tr>
                    {features.map((feature, i) => (
                        <tr key={i}>
                            <td style={{ cursor: 'pointer', }} title='' onClick={() => { removeFeature(feature); }}><div className='num'>{i + 1}</div><i className='icon fa-regular fa-trash-can fa-sm' /></td>
                            <td style={{ cursor: 'pointer', textAlign: 'left', }} title=' ' onClick={(e) => { copy(e.target.textContent); }}>{feature.get('addr') || '-'}</td>
                            <td style={{}}>{feature.get('jimok') || '-'}</td>
                            <td style={{ wordBreak: 'keep-all', }}>{getUse(feature)}</td>
                            <td style={{ cursor: 'pointer', textAlign: 'right', }} title=' ' onClick={(e) => { copy(e.target.textContent); }}>{feature.get('area') > 0 ? (feature.get('area') * coefficient).toLocaleString('ko-KR', { minimumFractionDigits: 1, maximumFractionDigits: 1, }) : '-'}</td>
                            <td style={{ cursor: 'pointer', textAlign: 'right', }} title=' ' onClick={(e) => { copy(e.target.textContent); }}>{getJiga(feature, gosiYear) > 0 ? (getJiga(feature, gosiYear) / coefficient).toLocaleString('ko-KR', { maximumFractionDigits: 0, }) : '-'}</td>
                            <td style={{}} colSpan={2}>{feature.get('owner').length > 0 ? feature.get('owner')[0].posesnSeCodeNm : '-'}{feature.get('owner').length > 1 ? `  ${feature.get('owner').length - 1}` : ''}</td>
                            <td style={{ border: 'none', cursor: 'pointer', padding: 0, }} className='btn' title=' ' onClick={() => { window.open(`https://www.eum.go.kr/web/ar/lu/luLandDet.jsp?isNoScr=script&mode=search&pnu=${feature.get('pnu')}`); }}><i className='fa-solid fa-arrow-up-right-from-square fa-sm' /></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </section >
    );
}

const landUseList = ['1', '2', '1', '2', '3', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''];
function getUse(feature) {
    const arry = [];
    for (let element of feature.get('use')) {
        const useName = element.prposAreaDstrcCodeNm;
        if (element.cnflcAt !== '' && landUseList.indexOf(useName) > 0 && arry.indexOf(useName) === -1) {
            arry.push(useName);
        }
    }
    return arry.length > 0 ? arry.join('\n') : '-';
}

function getUseforDownload(feature, type) {
    const arry = [];
    for (let element of feature.get('use')) {
        const data = element.prposAreaDstrcCodeNm;
        if (element.cnflcAtNm === type && arry.indexOf(data) === -1) {
            arry.push(data);
        }
    }
    return arry.length > 0 ? arry.join(' ') : '';
}

function getJiga(feature, year) {
    for (let element of feature.get('jiga')) {
        if (Number(element.stdrYear) === year) {
            return Number(element.pblntfPclnd);
        }
    }
    return 0;
}

function getOwnerforDownload(feature) {
    const arry = [];
    for (let element of feature.get('owner')) {
        const data = `${element.posesnSeCodeNm}(${element.resdncSeCodeNm})`;
        arry.push(data);
    }
    return arry;
}

function download(features) {
    if (features.length === 0) return;

    const date = new Date();
    const years = [...Array(date.getFullYear() - 1989 + 1)].map((_, i) => date.getFullYear() - i);
    let ownerCountMax = 1;

    const csv = features.map((feature, i) => {
        const num = Number(i) + 1;
        const addr = feature.get('addr') || '';
        const jimok = feature.get('jimok') || '';
        const use1 = getUseforDownload(feature, '');
        const use2 = getUseforDownload(feature, '');
        const use3 = getUseforDownload(feature, '');
        const area = [feature.get('area') || '', feature.get('area') * 0.3025 || ''];
        const jiga = years.map((year) => getJiga(feature, year));
        const owner = getOwnerforDownload(feature);
        ownerCountMax = owner.length > ownerCountMax ? owner.length : ownerCountMax;
        return [num, addr, jimok, use1, use2, use3, ...area, ...jiga, ...owner];
    });
    csv.unshift(['', '', '', '', '', '', 'm', '', ...years, ...Array(ownerCountMax).fill().map((_, i) => i + 1)]);
    csv.unshift(['', '', '', '', '', '', '', '', ...Array(years.length).fill(''), ...Array(ownerCountMax).fill('')]);

    const csvFile = new Blob(['\ufeff' + csv.join('\n')], { type: 'text/csv' });
    const link = document.createElement('a');
    link.href = window.URL.createObjectURL(csvFile);
    link.download = `__${date.getFullYear()}${(date.getMonth() + 1).toString().padStart(2, '0')}${date.getDate().toString().padStart(2, '0')}.csv`;
    link.click();
}

export default Element;
