import { create } from 'zustand';

export const useNotice = create((set, get) => ({
    notices: [''],
    setNotice: (text) => {
        set({ notices: [...get().notices, text] });
        //setTimeout(() => { get().removeNotice(); }, 2000);
    },
    removeNotice: () => set((state) => ({ notices: state.notices.slice(1) })),
}));
