import './css.css';
import { useEffect, useState } from 'react';
import { useNotice } from '../Header/notice';
import { useMap } from '../Map/store';

function Element() {
    const { notices, setNotice } = useNotice();
    const { setLabelType } = useMap();
    const [isOption, setIsOption] = useState(false);

    const [isScrollTop, setScrollTop] = useState(true);
    useEffect(() => {
        const mainElement = document.getElementById('main');
        const handleScroll = () => { setScrollTop(mainElement.scrollTop === 0); };
        mainElement.addEventListener('scroll', handleScroll);
        return () => { mainElement.removeEventListener('scroll', handleScroll); };
    }, []);

    return (
        <header id='header'>
            <div className='noise' />
            <nav>
                <ul>
                    <li>
                        <div className='item' onClick={() => { setMainScroll('map'); }}>
                            <div className='icon'><i className='fa-regular fa-map fa-lg' /></div>
                            <div className='title'></div>
                        </div>
                    </li>
                    <li>
                        <div className='item' onClick={() => { setIsOption(!isOption); }}>
                            <div className='icon'><i className='fa-solid fa-sliders fa-lg' /></div>
                            <div className='title'></div>
                        </div>
                        <div className={isOption ? 'options active' : 'options'}>
                            <div className='option'>
                                <div></div>
                                <div>
                                    <select name='labelType' defaultValue='num' onChange={(e) => { setLabelType(e.target.value); }}>
                                        <option value=''></option>
                                        <option value='num'></option>
                                        <option value='jibun'></option>
                                        <option value='jimok'></option>
                                    </select>
                                </div>
                            </div>
                        </div>
                    </li>
                    <div className='hr' />
                    <li>
                        <div className='item' onClick={() => { setMainScroll('list'); }}>
                            <div className='icon'><i className='fa-regular fa-rectangle-list fa-lg' /></div>
                            <div className='title'></div>
                        </div>
                    </li>
                    <div className='mg' />
                    <li>
                        <div className='item'>
                            <div className='icon'><i className='fa-regular fa-bell fa-lg' /></div>
                            <div className='title'></div>
                        </div>
                    </li>
                    <div className='hr' />
                    <li>
                        <div className='item' onClick={() => { setNotice('  ', 2000); }}>
                            <div className='icon'><i className='fa-regular fa-circle-user fa-lg' /></div>
                            <div className='title'></div>
                        </div>
                    </li>
                </ul>
            </nav>
            <div id='anchor' className={isScrollTop ? '' : 'rotate'} onClick={() => { setMainScroll(isScrollTop ? 'list' : 'map'); }}>
                <i className='fa-solid fa-arrow-down fa-beat-fade' />
            </div >
            <div id='notice'>
                {notices.map((notice, i) => (
                    <pre key={i} className='notice' style={{ bottom: `${(notices.length - 1 - i) * 4}rem`, }}>
                        {notice || ''}
                    </pre>
                ))}
            </div>
        </header >
    );
}

function setMainScroll(id) {
    document.getElementById(id).scrollIntoView({ behavior: 'smooth', block: 'start' });
}

export default Element;
