import './css.css';
import { useCallback, useEffect, useRef, useState } from 'react';
import { useMap } from '../Map/store';
import { useNotice } from '../Header/notice';

function Element() {
    const { setNotice } = useNotice();
    const { createMap, setMap, setView, searchMap, searchClear, searchDatas } = useMap();
    useEffect(() => { createMap(); }, [createMap]);

    const [mapType, setMapType] = useState(null);
    useEffect(() => { setMap(mapType); }, [mapType, setMap]);

    const searchRef = useRef();
    const [isFocusSearch, setFocusSearch] = useState(false);
    const search = useCallback((text) => { if (text) searchMap(text); }, [searchMap]);

    return (
        <div id='map'>
            <div className='noise' />
            <div id='mapDiv' />
            <div id='toolbar'>
                <div className='searchData'>{searchDatas.map((data, i) => (
                    <div key={i} className='data' onClick={() => { setView(data.point, 18); }}>
                        <div className='icon'>
                            <i className='fa-solid fa-location-dot fa-lg' />
                        </div>
                        <div className='addr'>
                            {data.parcel}
                            <br />
                            {data.road}
                        </div>
                    </div>
                ))}</div>
                <div className='tools'>
                    <div className='item' title='' onClick={() => {
                        if (!isFocusSearch) searchRef.current.focus();
                        setFocusSearch(!isFocusSearch);
                        searchClear();
                    }}>
                        <i className='fa-solid fa-search' />
                    </div>
                    <div className={isFocusSearch ? 'searchBox show' : 'searchBox'}>
                        <input ref={searchRef} className='search' type='search' placeholder='' autoComplete='off' onKeyDown={(e) => {
                            const target = e.target;
                            switch (e.key) {
                                case 'Enter':
                                    searchClear();
                                    search(target.value);
                                    break;
                                case 'Escape':
                                    searchClear();
                                    if (!target.value) setFocusSearch(false);
                                    target.value = null;
                                    break;
                                default:
                                    break;
                            }
                        }} />
                        <div className='searchX' onClick={() => {
                            searchClear();
                            const target = searchRef.current;
                            if (target.value) {
                                target.focus();
                                target.value = null;
                            } else setFocusSearch(false);
                        }}>
                            <i className='fa-solid fa-xmark' />
                        </div>
                    </div>
                    <div className='hr' />
                    <div className='item' title='' onClick={() => { setNotice('  '); }}>
                        <i className='fa-solid fa-ruler' />
                    </div>
                    <div className='item' title='' onClick={() => { setNotice('  '); }}>
                        <i className='fa-regular fa-circle-dot' />
                    </div>
                    <div className='item' title='' onClick={() => { setNotice('  '); }}>
                        <i className='fa-solid fa-vector-square' />
                    </div>
                    <div className='hr' />
                    <div className={mapType === null ? 'item active' : 'item'} title='' onClick={() => { setMapType(null); }}>
                        <i className='fa-solid fa-globe' />
                    </div>
                    <div className={mapType === 'Satellite' ? 'item active' : 'item'} title='' onClick={() => { setMapType('Satellite'); }}>
                        <i className='fa-solid fa-earth-asia' />
                    </div>
                    <div className={mapType === 'LandUse' ? 'item active' : 'item'} title='' onClick={() => { setMapType('LandUse'); }}>
                        <i className='fa-solid fa-table-cells' />
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Element;
