import { create } from 'zustand';
import fetchJsonp from 'fetch-jsonp';

import 'ol/ol.css';
import Map from 'ol/Map';
import View from 'ol/View';
import { Tile as TileLayer, Vector as VectorLayer, VectorTile as VectorTileLayer, Group as LayerGroup } from 'ol/layer';
import { TileWMS, XYZ, Vector as VectorSource, VectorTile as VectorTileSource } from 'ol/source';
import { GeoJSON, MVT } from 'ol/format';
import { Attribution } from 'ol/control';
import { Style, Fill, Stroke, Text } from 'ol/style';
import Draw, { createBox } from 'ol/interaction/Draw';

const key = 'A8901E28-B93C-3A14-B1C1-2FBC40EB22CA';

const satelliteLayer = new TileLayer({
    visible: false,
    source: new XYZ({
        url: `https://api.vworld.kr/req/wmts/1.0.0/${key}/Satellite/{z}/{y}/{x}.jpeg`,
    }),
});

const landUseLayerGroup = new LayerGroup({
    visible: false,
    layers: [
        new TileLayer({
            source: new TileWMS({
                url: 'https://api.vworld.kr/req/wms',
                params: { key: key, layers: 'lt_c_uq111', },
            }),
        }),
        new TileLayer({
            source: new TileWMS({
                url: 'https://api.vworld.kr/req/wms',
                params: { key: key, layers: 'lt_c_lhblpn', },
            }),
        }),
        new TileLayer({
            source: new TileWMS({
                url: 'https://api.vworld.kr/req/wms',
                params: { key: key, layers: 'lp_pa_cbnd_bubun', },
            }),
        }),
    ],
});

const hoverStyle = (feature) => {
    const label = feature.get(useMap.getState().labelType) || '';
    return new Style({
        fill: new Fill({ color: 'rgba(0, 0, 0, 0.5)', }),
        stroke: new Stroke({ color: 'rgba(0, 0, 0, 1)', width: 3, }),
        text: new Text({
            text: label || '',
            scale: 1.5,
            stroke: new Stroke({ color: '#ffffff', width: 3 }),
            overflow: true,
        }),
    })
};

const selectLayer = new VectorLayer({
    className: 'vector',
    source: new VectorSource(),
    style: (feature) => {
        const label = feature.get(useMap.getState().labelType) || '';
        return new Style({
            fill: new Fill({ color: 'rgba(0, 0, 0, 0.1)', }),
            stroke: new Stroke({ color: 'rgba(0, 0, 0, 1)', width: 3, }),
            text: new Text({
                text: label || '',
                scale: 1.5,
                stroke: new Stroke({ color: '#ffffff', width: 3 }),
                overflow: true,
            }),
        })
    },
});

const attribution = new Attribution({ collapsible: false, });

const drag = new Draw({ type: 'Circle', geometryFunction: createBox(), });

const view = new View({ center: [14200000, 4300000], zoom: 7, minZoom: 7, maxZoom: 24 });

const map = new Map({
    view: view,
    controls: [attribution],
    layers: [
        new TileLayer({ // 
            source: new TileWMS({
                url: 'https://api.vworld.kr/req/wms',
                params: { key: key, layers: 'lt_c_adsido', sld_body: `<StyledLayerDescriptor version="1.0.0"><NamedLayer><Name>sop:lt_c_adsido</Name><UserStyle><FeatureTypeStyle><Rule><PolygonSymbolizer><Fill><CssParameter name="fill">#FFFFFF</CssParameter></Fill></PolygonSymbolizer></Rule></FeatureTypeStyle></UserStyle></NamedLayer></StyledLayerDescriptor>`, },
            }),
        }),
        new TileLayer({ // 
            source: new TileWMS({
                url: 'https://api.vworld.kr/req/wms',
                params: { key: key, layers: 'lt_c_wkmstrm', sld_body: `<StyledLayerDescriptor version="1.0.0"><NamedLayer><Name>sop:lt_c_wkmstrm</Name><UserStyle><FeatureTypeStyle><Rule><PolygonSymbolizer><Fill><CssParameter name="fill">#EBF5FB</CssParameter></Fill></PolygonSymbolizer></Rule></FeatureTypeStyle></UserStyle></NamedLayer></StyledLayerDescriptor>`, },
            }),
        }),
        new TileLayer({ // 
            source: new TileWMS({
                url: 'https://api.vworld.kr/req/wms',
                params: { key: key, layers: 'lt_c_dgmainbiz', sld_body: `<StyledLayerDescriptor version="1.0.0"><NamedLayer><Name>sop:lt_c_dgmainbiz</Name><UserStyle><FeatureTypeStyle><Rule><PolygonSymbolizer><Fill><CssParameter name="fill">#FBEEE6</CssParameter></Fill></PolygonSymbolizer></Rule></FeatureTypeStyle></UserStyle></NamedLayer></StyledLayerDescriptor>`, },
            }),
        }),
        new VectorTileLayer({ //  
            source: new VectorTileSource({
                format: new MVT(),
                url: `https://api.vworld.kr/req/wmts/vector/getTile/${key}/traffic/{z}/{x}/{y}.pbf`
            }),
            style: (feature) => {
                const layerName = feature.get('layer');
                if (layerName && (layerName.includes('vl_rodway_ctln') || layerName.includes('vl_ex_mainrdcenl_l'))) return new Style({
                    stroke: new Stroke({ color: 'rgba(234, 234, 234, 1)', width: 5, }),
                });
                else if (layerName && (layerName.includes('vl_ex_mainrd_a') || layerName.includes('vl_ex_hwrda_a') || layerName.includes('vl_ex_narda_a') || layerName.includes('vl_ex_cityhwrda_a') || layerName.includes('vl_ex_localrda_a') || layerName.includes('vl_rodway_bndry') || layerName.includes('vl_arrfc'))) return new Style({
                    stroke: new Stroke({ color: 'rgba(234, 234, 234, 1)', width: 3, }),
                });
            },
        }),
        new VectorTileLayer({ //  
            source: new VectorTileSource({
                format: new MVT(),
                url: `https://api.vworld.kr/req/wmts/vector/getTile/${key}/traffic/{z}/{x}/{y}.pbf`
            }),
            style: (feature) => {
                const layerName = feature.get('layer');
                if (layerName && (layerName.includes('vl_rodway_ctln') || layerName.includes('vl_ex_mainrdcenl_l'))) return new Style({
                    stroke: new Stroke({ color: 'rgba(255, 255, 255, 1)', width: 2, }),
                });
                else if (layerName && (layerName.includes('vl_ex_mainrd_a') || layerName.includes('vl_ex_hwrda_a') || layerName.includes('vl_ex_narda_a') || layerName.includes('vl_ex_cityhwrda_a') || layerName.includes('vl_ex_localrda_a') || layerName.includes('vl_rodway_bndry') || layerName.includes('vl_arrfc'))) return new Style({
                    fill: new Fill({ color: 'rgba(255, 255, 255, 1)', }),
                });
            },
        }),
        new VectorTileLayer({ // , 
            source: new VectorTileSource({
                format: new MVT(),
                url: `https://api.vworld.kr/req/wmts/vector/getTile/${key}/traffic/{z}/{x}/{y}.pbf`
            }),
            style: (feature) => {
                const layerName = feature.get('layer');
                if (layerName && (layerName.includes('vl_ex_subwa_a'))) return new Style({
                    fill: new Fill({ color: 'rgba(255, 0, 0, 0.2)', }),
                    stroke: new Stroke({ color: 'rgba(255, 0, 0, 1)', width: 1, }),
                });
                else if (layerName && (layerName.includes('vl_ex_subwln_l') || layerName.includes('vl_ex_ssubwln_l'))) return new Style({
                    stroke: new Stroke({ color: 'rgba(255, 0, 0, 1)', width: 1, }),
                });
                else if (layerName && (layerName.includes('vl_rlroad_ctln'))) return new Style({
                    stroke: new Stroke({ color: 'rgba(0, 255, 0, 1)', width: 1, }),
                });
            },
        }),
        satelliteLayer,
        landUseLayerGroup,
        /*
        new VectorTileLayer({ // 
            source: new VectorTileSource({
                format: new MVT(),
                url: `https://api.vworld.kr/req/wmts/vector/getTile/${key}/poi/{z}/{x}/{y}.pbf`
            }),
            style: (feature) => {
                const text = feature.get('poi_nm');
                if (text) return new Style({
                    text: new Text({ text: text, stroke: new Stroke({ color: 'rgb(255, 255, 255)', width: 2, }), overflow: true, }),
                });
                else return;
            },
        }),
        */
        selectLayer,
    ],
});

