document.addEventListener('keydown', (e) => { if ((e.shiftKey || e.ctrlKey) && !drag.getMap()) map.addInteraction(drag); });
document.addEventListener('keyup', (e) => { if (!e.shiftKey && !e.ctrlKey && drag.getMap()) map.removeInteraction(drag); });
drag.on('drawend', (e) => {
    const polygonCoord = e.feature.getGeometry().getCoordinates()[0];
    addFeatures(`POLYGON((${polygonCoord.map(([x, y]) => `${x} ${y}`).join(',')}))`);
});

map.on('singleclick', (e) => {
    if (e.originalEvent.shiftKey || e.originalEvent.ctrlKey) return;

    const pixel = e.pixel;
    const isSelectLayerClicked = map.forEachFeatureAtPixel(pixel, (feature, layer) => {
        if (layer === selectLayer) {
            removeFeature(feature);
            return true;
        }
    });
    if (isSelectLayerClicked) return;

    const coordinate = e.coordinate;
    addFeatures(`POINT(${coordinate[0]} ${coordinate[1]})`);
});

let hovered = null;
map.on('pointermove', (e) => {
    const isHovered = map.forEachFeatureAtPixel(e.pixel, (feature, layer) => {
        if (layer === selectLayer) {
            if (hovered && hovered !== feature) hovered.setStyle(undefined);
            hovered = feature;
            hovered.setStyle(hoverStyle);
            return true;
        }
    });
    map.getTargetElement().style.cursor = isHovered ? 'pointer' : '';
    if (!isHovered && hovered) {
        hovered.setStyle(undefined);
        hovered = null;
    }
});

const updateAttribution = () => attribution.attributions_ = [
    `${map.getView().getZoom().toFixed(1)}`,
    `${map.getView().getCenter()[0].toFixed()},${map.getView().getCenter()[1].toFixed()}`,
    `dxxgbae`,
];
updateAttribution();

const addFeatures = (geomFilter) => {
    fetchJsonp(`https://api.vworld.kr/req/data?key=${key}&request=GetFeature&data=lp_pa_cbnd_bubun&crs=EPSG:3857&geomFilter=${geomFilter}`)
        .then(response => { return response.json(); })
        .then(data => {
            if (data.response.result) {
                const features = new GeoJSON().readFeatures(data.response.result.featureCollection);
                for (let feature of features) {
                    if (selectLayer.getSource().hasFeature(feature)) removeFeature(selectLayer.getSource().getFeatureById(feature.getId()));
                    else selectLayer.getSource().addFeature(feature);
                }
                updateFeature();
            }
        })
        .catch(error => { console.error(error); });
};

const removeFeature = (feature) => {
    selectLayer.getSource().removeFeature(feature);
    updateFeature();
};

const updateFeature = async () => {
    const features = selectLayer.getSource().getFeatures();

    for (let feature of features) {
        if (feature.get('hasData')) continue;
        feature.set('hasData', true);
        feature.set('use', []);
        feature.set('jiga', []);

        const fetchData = async (endpoint) => {
            const url = `https://api.vworld.kr/${endpoint}?key=${key}&pnu=${feature.get('pnu')}&numOfRows=1000`;
            const response = await fetchJsonp(url);
            return await response.json();
        };

        try {
            const [ladfrlVOList, landUses, indvdLandPrices, possessions] = await Promise.all([
                fetchData(`ned/data/ladfrlList`),
                fetchData(`ned/data/getLandUseAttr`),
                fetchData(`ned/data/getIndvdLandPriceAttr`),
                fetchData(`ned/data/getPossessionAttr`)
            ]);

            if (ladfrlVOList.ladfrlVOList) {
                const data = ladfrlVOList.ladfrlVOList.ladfrlVOList[0];
                feature.set('jimok', data.lndcgrCodeNm);
                feature.set('area', data.lndpclAr);
            }
            if (landUses.landUses) {
                const data = landUses.landUses.field;
                feature.set('use', data);
            }
            if (indvdLandPrices.indvdLandPrices) {
                const data = indvdLandPrices.indvdLandPrices.field.reverse();
                feature.set('jiga', data);
            }
            if (possessions.possessions) {
                const data = possessions.possessions.field;
                feature.set('owner', data);
            }
        } catch (error) {
            console.error(error);
        }
    }

    features.sort((a, b) => { return a.get('pnu').localeCompare(b.get('pnu')); });
    features.forEach((feature, i) => { feature.set('num', Number(i + 1)); });
    useMap.getState().setFeatures(features);
    //console.log(features);
}

map.getView().on('change', updateAttribution);

export const useMap = create((set) => ({
    features: [],
    labelType: 'num',
    createMap: () => {
        if (map.getTarget()) return;
        map.setTarget('mapDiv');
    },
    setMap: (type) => {
        switch (type) {
            case 'Satellite':
                satelliteLayer.setVisible(true);
                landUseLayerGroup.setVisible(false);
                break;
            case 'LandUse':
                satelliteLayer.setVisible(false);
                landUseLayerGroup.setVisible(true);
                break;
            default:
                satelliteLayer.setVisible(false);
                landUseLayerGroup.setVisible(false);
                break;
        }
    },
    setView: (point = null, zoom = null) => {
        map.getView().animate({
            center: point ? point : view.getCenter(),
            zoom: zoom ? zoom : view.getZoom(),
            duration: 1000,
        });
    },
    setLabelType: (type) => set(() => ({ labelType: type || null, })),
    searchDatas: [],
    searchMap: async (text) => {
        if (!text) return;

        await fetchJsonp(`https://api.vworld.kr/req/search?key=${key}&request=search&query=${text}&type=address&category=parcel&crs=EPSG:3857`)
            .then(response => { return response.json(); })
            .then(data => {
                if (data.response.result) {
                    const datas = data.response.result.items;
                    for (let data of datas) {
                        useMap.getState().searchPush({
                            parcel: data.address.parcel || '',
                            road: data.address.road || '',
                            point: [data.point.x, data.point.y] || null,
                        });
                    }
                }
            })
            .catch(error => { console.error(error); });

        await fetchJsonp(`https://api.vworld.kr/req/search?key=${key}&request=search&query=${text}&type=address&category=road&crs=EPSG:3857`)
            .then(response => { return response.json(); })
            .then(data => {
                if (data.response.result) {
                    const datas = data.response.result.items;
                    for (let data of datas) {
                        useMap.getState().searchPush({
                            title: data.address.bldnm || '',
                            parcel: data.address.parcel || '',
                            road: data.address.road || '',
                            point: [data.point.x, data.point.y] || null,
                        });
                    }
                }
            })
            .catch(error => { console.error(error); });
    },
    searchPush: (data) => set((state) => ({ searchDatas: [...state.searchDatas, data], })),
    searchClear: () => set(() => ({ searchDatas: [], })),
    setFeatures: (features) => set(() => ({ features: features || [], })),
    removeFeature: (feature) => {
        selectLayer.getSource().removeFeature(feature);
        updateFeature();
    },
    removeAll: () => {
        selectLayer.getSource().removeFeatures(selectLayer.getSource().getFeatures());
        updateFeature();
    },
}));
